footer.sticky-footer {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 4%;
  line-height: 55px;
}

@media (min-width: 768px) {
  footer.sticky-footer {
    width: calc(100% - 250px);
  }
}

@media (min-width: 768px) {
  body.sidenav-toggled footer.sticky-footer {
    width: calc(100% - 55px);
  }
}



